
#resumePreview {
  color: dodgerblue;
  background: #FFF;
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.5);
  padding: 2em;
  color: #333;
  line-height: 1.2;
  overflow: auto;
}
#resumePreview * {
    box-sizing: border-box;
    font-variant: normal;
    font-weight: normal;
}
#resumePreview .skin {
    margin: 10px 0;
}
#resumePreview .skin .text {
      float: left;
      margin-right: 20px;
      line-height: 30px;
      background: #FFF;
      background: linear-gradient(-45deg, transparent 10px, #FFF 0) bottom right no-repeat, linear-gradient(-135deg, transparent 10px, #FFF 0) top right no-repeat;
      background-size: 100% 50%;
      padding: 0 30px 0 20px;
      cursor: pointer;
}
#resumePreview .skin li {
      float: left;
      cursor: pointer;
      width: 30px;
      height: 30px;
      border-radius: 50%;
      border: 2px solid #AFD3EB;
      margin: 0 10px;
}
#resumePreview .skin .skin-show-enter-active {
      transition: all .5s ease;
}
#resumePreview .skin .skin-show-leave-active {
      transition: all 0.8s cubic-bezier(1, 0.5, 0.8, 1);
}
#resumePreview .skin .skin-show-enter, #resumePreview .skin .skin-show-leave-active {
      -webkit-transform: translateY(-10px);
              transform: translateY(-10px);
      opacity: 0;
}
#resumePreview section + section {
    margin-top: 2em;
}
#resumePreview p {
    white-space: pre-line;
}
#resumePreview section {
    background: #EEE;
    border-left: 6px solid;
    box-shadow: 0 0 2px 0px rgba(0, 0, 0, 0.5);
    padding: 15px;
}
#resumePreview section:hover {
      box-shadow: 0 2px 10px 0px rgba(0, 0, 0, 0.5);
}
#resumePreview section h2:first-child {
      display: inline-block;
      padding: 0.2em;
      margin-bottom: 0.5em;
}
#resumePreview section[data-name="profile"] h1 {
    margin: 0.1em 0;
    font-size: 4em;
}
#resumePreview section[data-name="workHistory"] li + li,
  #resumePreview section[data-name="projects"] li + li,
  #resumePreview section[data-name="awards"] li + li {
    margin-top: 1em;
}
#resumePreview section[data-name="workHistory"] li h3,
  #resumePreview section[data-name="projects"] li h3,
  #resumePreview section[data-name="awards"] li h3 {
    border-bottom: 1px solid #999;
    padding-bottom: .3em;
    margin-bottom: .3em;
}
#resumePreview section[data-name="education"] li {
    line-height: 1.5;
}
#resumePreview section[data-name="education"] li h3 {
      display: inline-block;
}
#resumePreview section[data-name="contacts"] td:first-child {
    padding-right: 1em;
}



